import React from "react"

export const List = ({
    variant,
    items
})=>{

    const ListComponent = variant ==='ordered'?'ol':'li';

    return (
        <div>
            <ListComponent>
                {items.map((items,index)=>{
                    <li key={index}>{items}</li>
                })}
            </ListComponent>     
        </div>
    )
}